/**
 * Outbound link that handles firing google analytics events
 */
export default function ExternalLink({
    target = '_blank',
    href,
    rel = 'noopener noreferrer',
    ...rest
}) {
    // eslint-disable-next-line
    return <a
        target={target}
        rel={rel}
        href={href}
        {...rest}
    ></a>
}
